.tree-container {
  /* background: #FAFAFA; */
  background: white;
  padding-top: 10px;
  border-radius: 10px;
  border: solid rgb(138, 138, 138) 1px;
  height: 300px;
  width: 450px;
}

.tree-container p {
  color: #828A94;
  font-family: monospace;
  line-height: 1.3;
  padding-left: 10px;
  font-size: 12px;
}

svg.custom .node circle {
	fill: rgb(212, 212, 212);
	stroke: rgb(0, 0, 0);
	stroke-width: 1.5px;
}

svg.custom .node text {
  font-size: 12px;
	/* background-color: #444; */
  fill: rgb(0, 0, 0);
  /* stroke: #F4F4F4; */
  /* stroke-width: 0.5px; */
  /* text-shadow: 0px 0px 1px black; */
}

svg.custom .leaf-node {
	cursor: pointer;
}

svg.custom path.link {
	fill: none;
	stroke: rgb(85, 85, 85);
	stroke-width: 1.5px;
}

svg.custom .node circle.leaf-rev {
  fill: rgb(77, 77, 77);
}

svg.custom .node circle.deleted-rev {
  fill: #FF0032;
}

svg.custom .node circle.winning-rev {
  fill: rgb(0, 255, 98);
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "PT Serif", serif;
}

select.form-control,
textarea.form-control,
input.form-control {
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin-top: 15px;
}

.App .navbar-brand {
  font-weight: bold;
}

.Home .landing {
    padding: 80px 0;
    text-align: center;
}
  
.Home .landing h1 {
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
}
  
.Home .landing p {
    color: #999;
}
.json-pretty {
	line-height: 1.3;
	color: #828A94;
  background: #FAFAFA;
  border-radius: 10px;
  border: solid rgb(138, 138, 138) 1px;
  font-size: 12px;
  padding: 10px;
  height: 220px;
}

.json-pretty .json-key {
  color: #FF2000;
}

.json-pretty .json-string {
  color: #00B7EB;
}

.json-pretty .json-value {
	color: #00A313;
}

.json-pretty .json-boolean {
	color: #E000CD;
}

.json-container {
  width: 380px;
  margin-left: 20px;
}

.MainPage .landing {
    padding: 80px 0;
    text-align: center;
}
  
.MainPage .landing h1 {
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
}
  
.MainPage .landing p {
    color: #999;
}
.NotFound {
    padding-top: 100px;
    text-align: center;
  }
.LoaderButton .spinning.glyphicon {
    margin-right: 7px;
    top: 2px;
    -webkit-animation: spin 1s infinite linear;
            animation: spin 1s infinite linear;
  }
  @-webkit-keyframes spin {
    from { -webkit-transform: scale(1) rotate(0deg); transform: scale(1) rotate(0deg); }
    to { -webkit-transform: scale(1) rotate(360deg); transform: scale(1) rotate(360deg); }
  }
  @keyframes spin {
    from { -webkit-transform: scale(1) rotate(0deg); transform: scale(1) rotate(0deg); }
    to { -webkit-transform: scale(1) rotate(360deg); transform: scale(1) rotate(360deg); }
  }
@media all and (min-width: 480px) {
    .Login {
        padding: 60px 0;
    }

    .Login form {
        margin: 0 auto;
        max-width: 320px;
    }
}
@media all and (min-width: 480px) {
    .Signup {
      padding: 60px 0;
    }
  
    .Signup form {
      margin: 0 auto;
      max-width: 320px;
    }
  }
  
  .Signup form span.help-block {
    font-size: 14px;
    padding-bottom: 10px;
    color: #999;
  }
